
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'


import { mapGetters } from "vuex";


export default {
name: "Point",
components: {
Group,
Cell,
XHeader,

},
data() {
return {
normalImg: require("./img/default-user.png"),
items: [],
rank: {},
users: {}
};
},
methods: {},

computed: {
...mapGetters(["integralDetails", "integralRanks", "userInfo"])
},

created() {
/*this.$store.dispatch("getintegraldetails", {
id: this.$route.params.userid
});*/
},
activated() {
this.users = this.userInfo;
this.$store.dispatch("getintegraldetails", {
id: this.$route.params.userid
});
},

watch: {
integralDetails: function(val) {
this.items = val;
},
integralRanks: function(val) {
this.rank = val;
}
}
};



